.wrap {
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
    padding: 40px 20px;
    background-size: cover;
    background-position: top;
  }
  
  .img {
    width: 350px;
    box-shadow: 10px 10px 12px 19px rgba(24, 30, 36, 0.54);
  }
  
  .inner {
    max-width: 500px;
    color: white;
  }
  
  .titleFilm {
    font-size: 35px;
  }
  
  .goBackBtn {
    background-color: inherit;
    height: 40px;
    fill: white;
    border: none;
    transform: scale(1);
    outline: none;
    transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .goBackBtn:focus,
  .goBackBtn:hover {
    fill: rgb(191, 246, 50);
    transform: scale(0.9);
  }  